<template>
  <div class="index mescroll" id="list">
    <div class="list-data" id="listData">
      <div class="banner">
        <van-swipe :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item,index) in picList" :key="index">
            <img :src="$utils.dealImg(item.bimg)" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="top">
        <div class="index-classify">
          <grid :grid-array="classifyList" width="55px" height="55px" font-color="#666" borderRadius="50%" @goRouter="goRouter" />
        </div>
      </div>
      <div class="mid">
        <div class="title" v-if="false">为你而选</div>
        <div class="list" v-if="false">
          <div class="item" v-for="(item,index) in 4" :key="index" :style="index % 2 == 0?'margin-right:5px;':''">
            <img src="@/assets/img/default/defaultH.png" alt />
          </div>
        </div>
        <div class="title">附近商家</div>
        <div class="list1">
          <div class="item" v-for="(item,index) in shopList" :key="index">
            <img class="left" @click="toDetail(item.shopCode)" :src="$utils.dealImg(item.shopHeader)" alt />
            <div class="mid">
              <div class="flex">
                <p class="name">{{item.shopName}}</p>
                <div class="look" @click="toMerchant(item.shopCode)" v-if="item.shopIsweb == 1">
                  <p>去看看</p>
                  <img class="icon" src="../../assets/img/merchantsIcon/right2.png" alt="">
                </div>
              </div>
              <div class="flex">
                <div style="flex:2;">
                  <div class="labels" @click="toDetail(item.shopCode)">
                    <div class="label" v-for="(item1,index1) in item.shopTitles" :key="index1" v-show="item1">{{item1}}</div>
                  </div>
                  <!-- <p class="text" @click="toDetail(item.shopCode)">电话:{{item.shopMobile}}</p> -->
                  <p class="text" @click="toDetail(item.shopCode)">地址:{{item.shopAddr}}</p>
                  <p class="text" @click="toDetail(item.shopCode)">营业时间:{{item.shopYingye}}</p>
                </div>
                <div class="right">
                  <div class="btn" @click="contact(item.shopMobile)">
                    <img src="../../assets/img/merchantsIcon/telno1.png" alt="">
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Swipe, SwipeItem } from "vant";
import MeScroll from "mescroll.js";
import { getFoundData } from "@/api";
import Grid from "@/components/Grid";
export default {
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    Grid
  },
  data() {
    return {
      classifyList: [],
      picList: [],
      shopList: []
    };
  },
  methods: {
    goRouter(item) {
      this.$router.push({
        name: "foundList",
        query: { cateCode: item.cateCode }
      });
    },
    toDetail(id) {
      this.$router.push({
        name: "foundDetail",
        query: { id: id }
      });
    },
    toMerchant(shopCode) {
      this.$router.push({
        name: "merchantsIndex",
        query: {
          shopCode: shopCode
        }
      });
    },
    contact(telno) {
      window.location.href = `tel://${telno}`;
    },
    getData() {
      getFoundData().then(res => {
        if (res.code == 0) {
          this.picList = res.data.bannerList;
          res.data.cateList.map(item => {
            item.icon = item.cateImg;
          });
          this.classifyList = res.data.cateList;
          res.data.shopList.map(item => {
            item.shopTitles = item.shopTitle.split(",");
          });
          this.shopList = res.data.shopList;
        } else {
          this.$toast.fail(res.msg);
        }
      });
    }
  },
  mounted() {
    document.title = "联盟商户";
    this.getData();
  }
};
</script>
<style lang="scss" scoped>
.index {
  height: calc(100vh - 44px);
  background: #f8f9f9;
  .banner {
    position: relative;
    padding: 10px 0;
    .van-swipe-item {
      img {
        margin: 0 10px;
        width: calc(100% - 20px);
      }
    }
  }
  .mid {
    margin: 10px 0 0 0;
    background: #fff;
    .title {
      font-weight: bold;
      line-height: 30px;
      padding: 10px 10px 0;
    }
    .list {
      padding: 5px 10px 10px;
      overflow: hidden;
      .item {
        width: calc((100vw - 20px - 5px) / 2);
        display: inline-block;
        img {
          width: 100%;
        }
      }
    }
    .list1 {
      padding: 0 10px 10px;
      overflow: hidden;
      .item {
        padding: 15px 0;
        border-bottom: 1pt solid #efefef;
        background: #fff;
        display: flex;
        align-items: center;
        .left {
          width: 100px;
          // height: calc(100px / 1.316);
          margin-right: 10px;
          border-radius: 5px;
        }
        .mid {
          flex: 2;
          position: relative;
          .flex {
            display: flex;
            align-items: center;
            .name {
              font-size: 15px;
              color: #333;
              line-height: 20px;
              flex: 2;
            }
            .look {
              display: flex;
              align-items: center;
              p {
                color: #ea5050;
                font-size: 13px;
                margin-right: 3px;
                line-height: 15px;
              }
              img {
                width: 5px;
              }
            }
          }

          .labels {
            margin: 10px 0 0;
            .label {
              display: inline-block;
              font-size: 13px;
              margin: 0 10px 5px 0;
              background: #ffba60;
              padding: 3px 8px;
              border-radius: 10px;
              color: #fff;
            }
          }
          .text {
            font-size: 13px;
            color: #666;
            line-height: 20px;
          }
        }
        .right {
          text-align: right;
          margin: 5px 0 0;
          .btn {
            display: inline-block;
            img {
              width: 30px;
              height: 30px;
            }
          }
        }
      }
    }
  }
}
</style>
<style lang="scss">
.index {
  .mescroll-empty {
    transform: translateY(30%);
    padding: 0 0 20px;
  }
}
</style>
